var _ = require('./util');


/**
 * Ëß£Êûêstyle
 *
 * @param {String} css
 * @param {Function} onAttr Â§ÑÁêÜÂ±ûÊÄßÁöÑÂáΩÊï∞
 *   ÂèÇÊï∞Ê†ºÂºèÔºö function (sourcePosition, position, name, value, source)
 * @return {String}
 */
function parseStyle (css, onAttr) {
  css = _.trimRight(css);
  if (css[css.length - 1] !== ';') css += ';';
  var cssLength = css.length;
  var isParenthesisOpen = false;
  var lastPos = 0;
  var i = 0;
  var retCSS = '';

  function addNewAttr () {
    // Â¶ÇÊûúÊ≤°ÊúâÊ≠£Â∏∏ÁöÑÈó≠ÂêàÂúÜÊã¨Âè∑ÔºåÂàôÁõ¥Êé•ÂøΩÁï•ÂΩìÂâçÂ±ûÊÄß
    if (!isParenthesisOpen) {
      var source = _.trim(css.slice(lastPos, i));
      var j = source.indexOf(':');
      if (j !== -1) {
        var name = _.trim(source.slice(0, j));
        var value = _.trim(source.slice(j + 1));
        // ÂøÖÈ°ªÊúâÂ±ûÊÄßÂêçÁß∞
        if (name) {
          var ret = onAttr(lastPos, retCSS.length, name, value, source);
          if (ret) retCSS += ret + '; ';
        }
      }
    }
    lastPos = i + 1;
  }

  for (; i < cssLength; i++) {
    var c = css[i];
    if (c === '/' && css[i + 1] === '*') {
      // Â§áÊ≥®ÂºÄÂßã
      var j = css.indexOf('*/', i + 2);
      // Â¶ÇÊûúÊ≤°ÊúâÊ≠£Â∏∏ÁöÑÂ§áÊ≥®ÁªìÊùüÔºåÂàôÂêéÈù¢ÁöÑÈÉ®ÂàÜÂÖ®ÈÉ®Ë∑≥Ëøá
      if (j === -1) break;
      // Áõ¥Êé•Â∞ÜÂΩìÂâç‰ΩçÁΩÆË∞ÉÂà∞Â§áÊ≥®ÁªìÂ∞æÔºåÂπ∂‰∏îÂàùÂßãÂåñÁä∂ÊÄÅ
      i = j + 1;
      lastPos = i + 1;
      isParenthesisOpen = false;
    } else if (c === '(') {
      isParenthesisOpen = true;
    } else if (c === ')') {
      isParenthesisOpen = false;
    } else if (c === ';') {
      if (isParenthesisOpen) {
        // Âú®ÂúÜÊã¨Âè∑ÈáåÈù¢ÔºåÂøΩÁï•
      } else {
        addNewAttr();
      }
    } else if (c === '\n') {
      addNewAttr();
    }
  }

  return _.trim(retCSS);
}

module.exports = parseStyle;
